.box {
    background-color: #471ca9;
    border-radius: 20px;
    box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
    width: 380px;
    height: 460px;
    margin-top: 10px;
    margin-left: 10px;

}

.logo {
    margin-top: 20px;
    margin-left: 20px;
}

.picture {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 18px;


}

.userInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    gap: 16px;

}

.avatarBox {
    /* position: absolute; */
    /* top: 178px;
  left: 150px; */
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: -62px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    width: 80px;
    height: 80px;
    border: 7px solid #ebd8ff;
    box-shadow: 0px 4.39163px 4.39163px rgba(0, 0, 0, 0.06), inset 0px -2.19582px 4.39163px #AE7BE3, inset 0px 4.39163px 3.29372px #FBF8FF;
    border-radius: 50%;

}

.image {
    width: 80px;
    height: 80px;
    border-radius: 50%;

}

.text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    color: #EBD8FF;
}

.button {
    margin-left: auto;
    margin-right: auto;
    background-color: #ebd8ff;
    border-radius: 10.3px;
    color: #373737;
    width: 196px;
    height: 50px;
    padding: 14px, 28px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 1, 22%;
    cursor: pointer;
}

.buttonFollowing {
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(234, 231, 64);
    border-radius: 10.3px;
    color: #373737;
    width: 196px;
    height: 50px;
    padding: 14px, 28px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 1, 22%;
    cursor: pointer;
}