.btnWrapper {
    text-align: center;
}

.button {
    margin-left: auto;
    margin-right: auto;
    background-color: #ebd8ff;
    border-radius: 10.3px;
    color: #373737;
    width: 196px;
    height: 50px;
    padding: 14px, 28px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 1, 22%;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    cursor: pointer;
}

.button:hover,
.button:focus {
    background-color: rgb(75, 64, 234)
}