.list {
    display: flex;
    gap: 48px;
    flex-wrap: wrap;
    justify-content: center;
}

.button {
    padding: 14px 46px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #5cd3a8;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border-radius: 10.3108px;
    border: 0;
    font-weight: 600;
    font-size: 18px;
    text-transform: uppercase;
    line-height: 21.94px;
    color: #373737;
    cursor: pointer;
    margin-bottom: 10px;
    margin-top: 20px;
    transition: all 0.4s cubic-bezier(0.5, 0, 0.3, 1);
}

.button:hover {
    transform: scale(1.04);
    transition: all 0.4s cubic-bezier(0.5, 0, 0.3, 1);
    color: #fff;
}

.buttonBox {
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon {
    margin-left: 10px;
}